html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

/* Safari-specific styles */
@supports (-webkit-touch-callout: none) {
  html, body {
    background-color: #ffffff;
    background-color: var(--background);
    color: #171717;
    color: var(--foreground);
